<template>
  <div v-loading="loading">
    <cms-breadcrumb to="/timingtask/list" @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt collapse="true">
        <li>带 * 为必填项</li>
        <li>设置一个定时任务</li>
      </cms-prompt>
      <el-form ref="form" :rules="saveRules" :model="formData" label-width="210px">
        <el-form-item label="类型" prop="groupName" ref="groupName">
          <el-select
            v-model="formData.groupName"
            filterable
            allow-create
            default-first-option
            placeholder="请选择或填写">
            <el-option
              v-for="item in jobItem"
              :key="item"
              :label="item"
              :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item  label="任务标识"  prop="name" :error='errorMessage.name'>
          <el-input maxlength="150" v-model="formData.name" placeholder="请填写任务标识" ></el-input>
          <div class="gray">请填写一个全局唯一的标识码</div>
        </el-form-item>
        <el-form-item  label="任务名称"  prop="remark" :error='errorMessage.remark'>
          <el-input maxlength="150" v-model="formData.remark" placeholder="请填写任务名称" ></el-input>
        </el-form-item>
        <el-form-item label="状态" prop="status" :error="errorMessage.status">
          <cms-switch :onText="'启用'" size="large" :offText="'禁用'" v-model="formData.status"></cms-switch>
        </el-form-item>
        <el-form-item  label="执行类路径"  prop="classPath" :error='errorMessage.classPath'>
          <el-input maxlength="150" v-model="formData.classPath" placeholder="请填写任务名称" ></el-input>
        </el-form-item>
        <el-form-item label="执行周期" prop="cron" :error='errorMessage.cron'>
          <el-input v-model="formData.cron" auto-complete="off" placeholder="请选择表达式">
              <el-button slot="append" v-if="!showCronBox" icon="el-icon-arrow-up" @click="showCronBox = true" title="打开图形配置"></el-button>
              <el-button slot="append" v-else icon="el-icon-arrow-down" @click="showCronBox = false" title="关闭图形配置"></el-button>
          </el-input>
        </el-form-item>
        <el-form-item style="margin-top: -10px; margin-bottom:0px;width:780px;">
          <cron v-if="showCronBox" v-model="formData.cron"></cron>
          <!-- <span style="color: #E6A23C; font-size: 12px;">corn从左到右（用空格隔开）：秒 分 小时 月份中的日期 月份 星期中的日期 年份</span> -->
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="save(false)" v-color>保存并新增</cms-button>
        <cms-button @click="save(true)" v-color>保存</cms-button>
        <cms-button type="rest" @click="rest">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { jobSave, jobCheckJobName, jobFindGroupName } from '@/api/setting/timingTask.js'
import cron from './cron/cron'
var defaultData = {
  'classPath': '',
  'cron': '',
  'groupName': '',
  'name': '',
  'status': true,
  'params': '',
  'remark': ''
}

export default {
  name: 'timingTaskSave',
  mixins: [form],
  components: {
    cron
  },
  data() {
    var ischeckName = (rule, value, callback) => {
      // 验证区域code唯一
      jobCheckJobName({
        'jobName': value,
        'id': ''
      }).then(res => {
        if (res.code === 200) {
          if (res.data) {
            callback()
          } else {
            callback(new Error('任务标识已存在，不允许重复添加'))
          }
        }
      })
    }
    return {
      jobItem: [], // 物流公司列表
      showCronBox: false,
      formData: Object.assign({}, defaultData),
      saveRules: {
        // 新增规则
        name: [rules.required('任务标识不能为空'), rules.custom(ischeckName)],
        classPath: [rules.required('类路径不能为空')],
        groupName: [rules.required('分组名不能为空')],
        status: [rules.required('请选择是否开启', 'change')],
        remark: [rules.required('任务名称不能为空')],
        cron: [rules.required('表达式不能为空')]
      }
    }
  },
  methods: {
    save(state) {
      // 添加
      const params = Object.assign({}, this.formData)
      this.saveForm(
        jobSave,
        params,
        state,
        '/timingtask/list',
        'list'
      )
    },
    getJobItem() {
      // 获取job分组名
      jobFindGroupName().then(res => {
        this.jobItem = res.data
      })
    }
  },
  activated() {
    this.autoFocus()
    if (this.$route.query.type === 'save') {
      this.formData = Object.assign({}, defaultData)
    }
  },
  created() {
    this.getJobItem()
  }
}
</script>
<style>
</style>




